.manager {
  box-sizing: border-box;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.5);
  background-color: #fff;
  border-radius: 10px;
  width: 90%;
  height: 96%;
  margin: 0 auto;
  padding: 30px;
  display: flex;
  flex-direction: column;
  background: #fff;
  /* note color */
}
.header {
  display: flex;
  height: 50px;
  margin-bottom: 15px;
  background: transparent;
}
.managerTitle {
  flex: 1;
  text-align: center;
  font-size: 28px;
  line-height: 50px;
}
.iconContainer {
  cursor: pointer;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  outline: none;
  border: 2px solid transparent;
  opacity: 0.8;
}
.iconContainer:hover {
  opacity: 1;
  border: 2px solid rgba(0, 0, 0, 0.3);
}
.icon {
  font-size: 20px;
}
/* manager header */

.searchContainer {
  margin-bottom: 20px;
  height: 0;
  text-align: right;
  overflow: hidden;
  transition: 0.1s ease-out;
}
.searchBar {
  border: 1px solid #000;
  display: inline-block;
  height: 100%;
}
.searchText {
  font-size: 14px;
  line-height: 14px;
  height: 100%;
  outline: none;
  border: none;
  padding: 0 5px;
}
.searchBtn {
  width: 50px;
  height: 100%;
  outline: none;
  border: none;
  cursor: pointer;
  opacity: 0.8;
}
.searchBtn:hover {
  opacity: 1;
}
.searchIcon {
  font-size: 14px;
}
/* search bar */

.option {
  display: flex;
  margin-bottom: 30px;
  background: transparent;
  justify-content: space-between;
}
.sortContainer {
  height: 100%;
  flex-basis: 40%;
  border: 1px solid #000;
  border-radius: 0 10px 10px 0;
  padding: 5px;
}
.sort {
  cursor: pointer;
  width: 100%;
  height: 100%;
  background: transparent;
  outline: none;
  border: 1px solid transparent;
}
.sort:hover {
  border: 1px solid rgba(0, 0, 0, 0.5);
}
.setting {
  flex-basis: 60%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid #000;
  border-radius: 10px 0 0 10px;
  padding: 5px;
}
.settingTitle {
  font-size: 18px;
  line-height: 30px;
}
.fontTitle,
.colorTitle {
  font-size: 14px;
  font-weight: bold;
  line-height: 30px;
}
.colorContainer {
  display: flex;
}
.colorTitle {
  margin-right: 5px;
}
.fontSize {
  cursor: pointer;
  outline: none;
  border: 1px solid transparent;
  background: transparent;
}
.fontSize:hover {
  border: 1px solid rgba(0, 0, 0, 0.5);
}
.notesContainer {
  flex: 1;
  min-height: 0;
}
.notes {
  width: 100%;
  height: 100%;
  border: 1px solid black;
  overflow-y: auto;
}

@media screen and (max-width: 991px) {
  .manager {
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .option {
    flex-direction: column;
  }
  .setting {
    border-radius: 10px 10px 0 0;
  }
  .sortContainer {
    border-top: none;
    border-radius: 0 0 10px 10px;
    padding: 5px 30px;
  }
}
@media screen and (max-width: 591px) {
  .fontContainer {
    display: flex;
    flex-direction: column;
  }
  .colorContainer {
    flex-direction: column;
  }
  .colorTitle {
    margin: 0;
    text-align: center;
  }
}
