.editor {
  width: 100%;
  height: 100%;
  flex-basis: 50%;
  border-right: 1px solid lightgrey;
  padding: 20px;
  overflow-y: auto;
}
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 18px;
  color: #385461;
}

@media screen and (max-width: 991px) {
  .editor {
    margin-bottom: 10px;
  }
}
